@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
* {
  margin: 0;
  font-family: "Rubik";
  padding: 0;
  box-sizing: border-box;
}
:root {
  --VeryDarkGray: hsl(0, 0%, 17%);
  --DarkGray: hsl(0, 0%, 59%);
  --white: white;
  --radius: 1rem;
}

html {
  max-height: 100vh;
}

body {
  height: 100vh;
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
  font-weight: 500;
  padding: 1rem 0 1.5rem;
  text-align: center;
  color: white;
  color: var(--white);
}

h4 {
  font-weight: 500;
  max-width: 80%;
}

small {
  text-transform: uppercase;
  font-weight: 700;
  color: hsl(0, 0%, 59%);
  color: var(--DarkGray);
}
.app,
#root {
  max-height: 100vh;
}
.content {
  height: 30vh;
  background-image: url(/static/media/pattern-bg.a60a3c85.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.data-container {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  border-radius: var(--radius);
  background: var(--white);
  margin: 1rem auto;
  position: relative;
  z-index: 1;
  max-width: 80%;
}
.data-info {
  text-align: center;
  padding: 0.5rem;
  width: 200px;
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  grid-row-gap: 1rem;
  row-gap: 1rem;
}

@media (min-width: 768px) {
  .data-container {
    flex-direction: row;
    height: 200px;
    padding: 1rem;
    justify-content: space-around;
  }
}
@media (min-width: 1080px) {
  .data-info > small,
  h4 {
    max-width: 200px;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
}

input {
  padding: 1rem 1rem 1rem 2rem;
  outline: none;
  font-size: 18px;
  width: calc(100% - 55px);
  border: none;
  border-radius: var(--radius);
}
input:placeholder {
  color: var(--VeryDarkGray);
}
.info-container {
  width: 100%;
}
.input-container {
  border-radius: var(--radius);
  margin: 0 auto;
  width: 80%;
  max-width: 700px;
  background-color: var(--white);
}
.input-btn {
  cursor: pointer;
  border: none;
  height: 55px;
  border-radius: 0 var(--radius) var(--radius) 0;
  width: 55px;
  background-color: black;
  color: var(--white);
  transition-duration: 0.5s;
}
.input-btn:hover {
  background-color: rgb(59, 59, 59);
}

